import React from "react";

import groupings from "../controller/grouping-controller.js";
import uid from "../util/uid.js";

export default class Page extends React.Component {
    constructor(props) {
        super(props);

        this.id = uid();

        groupings.bindHeight(this.id);
    }
    render() {
        return (
            <div
                style={{
                    overflow: "hidden",
                    height: groupings.height().toString()
                }}
                id={this.id}
            >
                <div
                    style={{
                        margin: 0,
                        padding: 0
                    }}
                >
                    <div {...this.props}/>
                </div>
            </div>
        );
    }
}